.streak-photo{
    width: 40%;
    margin: auto;
   
  
    margin-top: 40px;
   
  }
  .streak-photo img{
    width: 100%;
  }

  
   .stacklanguage{
        
   
     width: 70%;
     margin: auto;
     display: flex;
  
      
      gap: 20px;
      justify-content: center;
      
       padding-top: 40px;
    
      
     
    
   
     
     
  
  
    

}

 
   
    
 


@media screen and (max-width:420px) {

  .streak-photo{
    width: 97%;
    margin: auto;
  
    margin-top: 40px;
   
  }
  .streak-photo img{
    width: 90%;
  }

  
  .stacklanguage{
        
     width: 96%;
     margin: auto;
     display: flex;
     flex-direction: column;
     
     gap:20px;
   
     padding-top: 20px;
     border: 1px solid white;
  
  
    

}
 .stacklanguage img{
  width: 90%;
   margin: auto;
 }
 .streak-photo img{
  width: 50%;
  margin: auto;
 }
    
    
 } 



 @media screen and (max-width:820px) {

    .streak-photo{
      width: 70%;
      margin: auto;
    
      margin-top: 40px;
     
    }
    .streak-photo img{
      width: 100%;
    }
  
    
    .stacklanguage{
          
       width: 99%;
       margin: auto;
       display: flex;
       flex-direction: column;
       
       gap:20px;
     
       padding-top: 40px;
    
    
      
  
  }
   .stacklanguage>img{
    width: 99%;
     margin: auto;
   }

  
      
      
   } 